body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.addBtn {
  margin: auto;
  display: block;
  background: #98bbff;
  color: #0e5ec3;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  outline: none;
  font-weight: 600;
  box-shadow: 0px 5px 4px 0px #1231a359;
}
#root {
  > .addBtn {
    padding: 10px 15px;
    font-size: 1.3rem;
    margin-top: 1rem;
  }
}
.container {
  width: 98%;
  padding-top: 10px;
  margin: auto;
  
}
@media (min-width: 767px) {
  .container {
    width: 720px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 1000px;
  }
}
@media (max-width: 767px) {
  .plate > div {
    flex-direction: column;
    align-items: normal !important;
    justify-content: center;
  }
  .collection > body {
    justify-content: normal;
  }
  .collection > .head input {
    font-size: 20px !important;
    max-width: 170px;
  }
  .plate .body.collapsed {
    max-width: max-content;
    max-height: 0;
  }
}
.deleteBtn {
  color: #9b0505;
  border: none;
  cursor: pointer;
  font-size: 18px;
  width: 20px;
  height: 20px;
  text-transform: lowercase;
  background: #ffa2a2;
  border-radius: 50%;
}
.collection {
  margin-bottom: 10px;
  .head {
    display: flex;
    align-items: center;
    gap: .5rem;
    margin-bottom: .5rem;
    min-height: 52px;
    .collapseBtn {
      width: 18px;
      height: 16px;
      position: relative;
      span {
        display: block;
        position: absolute;
        width: 100%;
        height: 2px;
        transition: .5s;
        left: 0;
        background-color: black;
        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: 50%;
          transform: translateY(-50%);
        }
        &:nth-child(3) {
          bottom: 0
        }
      }
      &.collapsed {
        span {
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  input {
    outline: none;
    border: none;
    padding: 5px;
    border-bottom: 2px solid transparent;
    &:focus {
      border-color: #0099ff;
    }
  }
}
.body {
  transition: 1s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  max-height: fit-content;
  &.collapsed {
    max-height: 0;
  }
}
> .addBtn {
  font-size: 1rem;
  padding: 5px 10px;
}
}
.plate {
  padding-left: 3rem;
  border-left: 2px solid #1c86ff;
  position: relative;
  padding-bottom: 10px;
  > div {
    display: flex;
    align-items: center;
    gap: .5rem;
    border: 3px solid #0075ff;
    border-radius: 10px;
    padding: 10px;
  }
  input[type='text'] {
    max-width: 100px;
  }
  &:last-of-type {
    border: none;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      background-color: #1c86ff;
      height: calc(100% - 30px);
      width: 2px;
    }
  }
  &::before {
    content: '';
    position: absolute;
    left: -2px;
    bottom: 20px;
    height: 30px;
    width: 3rem;
    border-left: 2px solid #1c86ff;
    border-bottom: 2px solid #1c86ff;
    border-bottom-left-radius: 16px;
  }
  .body {
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    transition: 1s max-width;
    gap: 10px;
    max-width: calc(100% - 230px);
    overflow: hidden;
    &.collapsed {
      max-width: 0;
      max-height: 0;
    }
  }
  .addColor {
    display: flex;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 3px dashed #a7a7a7;
    background: white;
    justify-content: center;
    align-items: center;
    color: #a7a7a7;
    font-size: 2.5rem;
    cursor: pointer;
  }
  .color {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: 0px 3px 7px #00000050;
    cursor: pointer;
  }
  .overlay {
    background-color: #15151534;
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 5;
    .editBox {
      background-color: #fff;
      border-radius: 15px;
      box-shadow: 2px 4px 15px #00000050;
      max-width: 500px;
      position: absolute;
      left: 50%;
      min-width: 400px;
      top: 50%;
      transform: translate(-50%, -50%);
      padding: 15px;
      display: flex;
      flex-direction: column;
      gap:1rem;
    .head {
      justify-content: space-between;
      input {
        min-width: 180px;
      }
    }
    .text {
      font-size: 2rem;
      padding: 15px;
      border-radius: 7px;
    }
    .colors {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 10px;
      > div {
        min-height: 110px;
        position: relative;
      }
      label {
        width: 75px;
        height: 75px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        z-index: 100;
        border-radius: 50%;

        box-shadow: 0px 3px 7px #00000050;
        &::before {
          content: '';
          position: absolute;
          width: 10px;
          height: 10px;
          background: inherit;
      }
        input {
          width: 0;
          height: 0;
          appearance: none;
          z-index: -1;
        }
      }
      .mainBtn {
        font-size: 12px;
        padding: 3px 5px;
        background-color: #8bd0ff;
        color: #0966a3;
        border-radius: 3px;
        cursor: pointer;
        position: absolute;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .footer {
      display: flex;
      gap: 10px;
      justify-content: center;
      align-items: center;
      div {
        border-radius: 10px;
        font-size: 24px;
        padding: 10px 20px;
        text-align: center;
        flex-grow: 1;
        color: #ebfff6;
        height: fit-content;
        &.saveBtn {
          background-color: #25ff99;
        }
        &.deleteBtn {
          background-color: #ff2f52;
        }
      }
    }
    }
  }
}